import { Navigate } from "react-router-dom";
import React from "react";

interface PrivateRouteProps {
  children: React.ReactNode;
}

const PrivateRoute = ({ children }: PrivateRouteProps) => {
  // Rcupration du token dans localStorage
  const token = localStorage.getItem("token");

  // Si un token existe, on peut le dcoder pour rcuprer le rle (par exemple)
  let isAdmin = false;
  if (token) {
    try {
      const decodedToken = JSON.parse(atob(token.split('.')[1])); // Dcodage du token
      isAdmin = decodedToken.role === "admin"; // Vrification du rle
    } catch (error) {
      console.error("Erreur lors du dcodage du token", error);
    }
  }

  // Si le token existe et que l'utilisateur est admin, on permet l'accs
  if (token && isAdmin) {
    return <>{children}</>;
  }

  // Sinon, on redirige vers la page de connexion
  return <Navigate to="/login" />;
};

export default PrivateRoute;
